*{margin: 0; padding: 0;}
a {text-decoration: none;}
li {list-style: none;}


/* ------ SPA page lnb ------ */


.navbar-brand {
  cursor: pointer;
}


.nav_bg {
  width: 100%; height: 80px;
  background-color: rgba(40, 56, 75, 90%);
  border-radius: 50px;
  line-height: 35px;
  text-align: center;
  margin: 60px auto;
  position: sticky;
  top: 0;
  z-index: 1;
}

.nav_bg .nav {
  justify-content: center;
}

.nav_bg a {
  /* border: 1px solid #2d2d2d; */
  background-color: linear-gradient(0deg, rgb(40, 56, 75) 0%, rgb(40, 56, 75) 100%);
  border-radius: 20px;
  margin: 1% 20px;
  box-shadow: inset 2px 2px 2px 0px rgba(255,255,255,.5),
  0px 0px 20px 5px rgba(45,45,45,20%),
  0px 0px 20px 0px rgba(250,250,250,20%);
  color:#f1f1f1;
  font-weight: bold;
}

.nav_bg a:hover {
  text-decoration: underline;
  color:#f1f1f1;
}

.nav_bg .active {
  background-color: #f1f1f1;
  color: black;
}

.nav_bg .active:hover {
  color: #2d2d2d;
}


/* ------ vs_main ------ */

.carousel {
  padding-bottom: 60px;
}

.carousel-indicators [data-bs-target]{
  width: 200px; height: 10px;
}

.vs_main {
  display: flex;
  justify-content: center;
}

.vs_main .vs_img {
  /* width: 616px; height: 353px; */
  width: 40%;
  margin-right: 10px;
}


.vs_main .vs_img img {
  width: 100%; height: 100%;
}


.thumb_vs_main {
  width: 40%;

}

.thumb_vs_main h3 {
  color: #f1f1f1;
  margin: 10px 0;
}

.thumb_vs_main .thumb_img_box {
  display: flex;
  flex-wrap: wrap;
  width: 80%;
  margin: 0 auto;
}

.thumb_vs_main .thumb_img_box .vs_thumb_img {
  width: calc(50% - 10px);
  margin-bottom: 10px; 
}

.thumb_vs_main .thumb_img_box .vs_thumb_img:nth-child(2n) {
  margin-left: 10px;
}

.thumb_vs_main .thumb_img_box .vs_thumb_img.pick {
  border: 2px solid #f1f1f1;
  padding: 1%; box-sizing: border-box;
}

.thumb_vs_main .vs_thumb_img img {
  width: 100%; height: 100%;
}


/* ------ add Daily Picker ------ */


.daily_view {
  position: fixed;
  top: 60px;
  right: -151px;
  transition: all 0.3s;
}

.daily_view:hover {
  right: 0;
}

.daily_view .daily_view_btn {
  width: 30px; height: 180px;
  border: 1px solid black;
  position: absolute;
  left: -21%;
  background-color: #20232a;
  border-radius: 5px;
}

.daily_view .daily_view_btn p {
  color: #f1f1f1;
  font-weight: 300;
  font-size: 20px;
  margin-top: 10px;
}

.daily_view .daily_p {
  width: 100%;
  padding: 1%;
  margin-bottom: 1%;
  background-color: #f1f1f1;
  border-bottom: 1px solid #ccc;
  font-weight: bold;
}

.daily_view .daily_view_box{
  width: 150px;
  border: 1px solid #ccc;
  position: relative;
  background-color: #f1f1f1;
}

.daily_view .daily_view_box .daily_img_box {
  padding: 50% 0;
}

.daily_view .daily_view_box .daily_img_box .thumb_image {
  position: relative;
  margin: 20px 0;
}

.daily_view .daily_view_box .daily_img_box .thumb_image .view_img_del {
  width: 30px; height: 30px;
  position: absolute;
  top: -15px;
  right: 0px;
  background-color: #fff;
  border-radius: 50px;
  border: 1px solid #20232a;
  cursor: pointer;
  font-weight: bold;
  text-align: center;
  line-height: 25px;
}

.daily_view .daily_view_box .daily_img_box .thumb_image .daily_view_img {
  display: block;
  width: 130px; height: 100px;
  margin: 10px auto;
}

.daily_view .daily_view_box .daily_img_box .thumb_image .daily_view_img img {
  width: 100%; height: 100%;
}



/* ------ component style ------ */

.game_list {
  margin: 60px 0;
}
/* .game_item {
  display: flex;
  border:  1px solid #eee;
  box-shadow: 0 0 5px rgba(0, 0, 0, 70%);
  margin-bottom: 20px;
} */
.game_item .item_info {
  width: 100%;
  text-align: left;
  padding: 2%; box-sizing: border-box;
}

.tag_list {
  width: 100%;
  padding: 0;
}

.tag_list li {
  display: inline-block;
  padding: 1% 2%;
  border: 1px solid beige;
  background-color: rgba(190, 190, 190, 30%);
  border-radius: 10px;
  margin-right: 10px; 
}

.game_item .item_info p {
  margin-bottom: 0;
}

.game_item .item_info .price {
  display: flex;
  justify-content: right;
  margin-top: 10px;
}

.game_item .item_info .price .free_play {
  background-color: rgba(190, 190, 190, 30%);
  padding: 1%;
}

.game_item .item_info .price .discount_per {
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  width: 80px;
  border:1px solid #ccc;
  margin-right: 10px;
  color: #01b053;
}

.game_item .item_info .price .del_cost {
  position: relative;
  font-size: 15px;
  text-align: right;
}

.game_item .item_info .price .del_cost::before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  border-bottom: 1px solid #2d2d2d;
  transform: skewY(-15deg);
}

.game_item .item_info .price .cost_result {
  padding: 0 1%; box-sizing: border-box;
  background-color: rgba(180, 180, 180, 30%);
}

.game_item .item_info .price .cost_result .discount_cost {
  font-size: 18px;
  font-weight: 500;
  color: #01b053;
}

.game_item .item_info .price .item_cost{
  background-color: rgba(180, 180, 180, 30%);
  padding: 1%; box-sizing: border-box;
}


/* ------ Cart component Style ------ */

.path {
  text-align: left;
  margin: 20px 0;
}

hr {
  margin-bottom: 50px;
}

.cart_name {
  text-align: left;
}

.table th {
  height: 30px;
}

.cart_item_list .cart_item_img {
  width: 100px; height: 100%;
  padding: 1% 0; box-sizing: border-box;
}

.cart_item_list .cart_item_img img {
  width: 100%; height: 100%;
}

.cart_item_list .cart_item_title {
  /* width: calc(100% - 150px); */
  font-size: 18px;
  padding-top: 50px;
}

.cart_item_list .c_del_cost,
.cart_item_list .cart_discount,
.cart_item_list .c_result_cost,
.cart_item_list .c_item_cost,
.cart_item_list td:nth-child(7)
 {
  padding-top: 50px;
}

.cart_item_list .del_txt {
  cursor: pointer;
}

.cart_item_list .c_result_cost {
  width: 100px;
}

.count_btn {
  height: 130px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.count_btn .count {
  width: 50px;
  margin-top: 13px;
}

.count_btn button {
  width: 30px; height: 30px;
  border-radius: 50px;
}

.last_total td {
  height: 100px;
  font-size: 25px;
}

.last_total td:nth-child(6) {
  width: 140px;
}


.last_total {
  position: relative;
}

.last_total .cart_zero_msg {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  padding-bottom: 100px;
  border-bottom: 1px solid #ccc;
}

.order_btn {
  overflow: hidden;
}

.order_btn button{
  float: right;
  padding: 1%; box-sizing: border-box;
  margin: 1%;
  border-radius: 10px;
}

/* ------ detail_page ------ */

.flex_detail_main {
  display: flex;
  justify-content: space-between;
}

.item_detail {
  width: 450px; height: 100%;
  border: 1px solid #ccc;
  position: relative;
}

.item_detail .item_main_img {
  width: 450px;
}

.item_detail .item_main_img img{
  width: 100%;
}

.item_detail_info .detail_desc {
  margin: 2% 0; box-sizing: border-box;
  text-indent: 8px;
  height: 150px;
}

.item_detail_info .detail_info_txt {
  text-align: left;
  margin-left: 20px;
}


.item_detail_info .detail_info_txt .detail_discount {
  padding: 2%; box-sizing: border-box;
  font-size: 30px;
  color: #01b053;
  font-weight: 500;
  background-color: #20232a;
  box-shadow: inset 1px 1px 2px 0px rgba(255, 255, 255, 50%);
  position: absolute;
  top: -25px;
  right: -30px;
}